import { useState } from "react";
import {dbService, storageService } from "../routes/fnbase";
import { v4 as uuidv4 } from "uuid";
import { addDoc, getDoc , collection } from "firebase/firestore";
import { ref, uploadString ,getDownloadURL } from "@firebase/storage";

import {useEffect} from "react";

const NweetFactory = ({ userObj }) => {
    
    const [nweet, setNweet] = useState("");
    const [attachment, setAttachment] = useState("");

    const onSubmit = async (event) => {
        event.preventDefault();
        let attachmentUrl = "";
        if (attachment !== "") {
        //   
        const fileRef = ref(storageService, `${userObj.uid}/${uuidv4()}`);
        //storage     
        const uploadFile = await uploadString(fileRef, attachment, "data_url");
        console.log(uploadFile);
        //storage   URL  
        attachmentUrl = await getDownloadURL(uploadFile.ref);
        }


              //,    firestore 
        const nweetPosting = {
            //text : nweets,
            nweet,
            createdAt: Date.now(),
            creatorId: userObj.uid,
            attachmentUrl,
            };
            await addDoc(collection(dbService, "nweets"), nweetPosting);
            setNweet("");
            setAttachment("");
            };
    
    
           
        const onChange = ({ target: { value } }) => {
            setNweet(value);
            };
         const onFileChange = (event) => {

                //console.log(event.target.files);
                const {
                    target: { files },

                } = event;
                const theFile = files[0];

                const reader = new FileReader();
                reader.onloadend = (finishedEvent) => {
                  //console.log(finishedEvent);
                  const {
                    currentTarget: { result },
                  } = finishedEvent;
                  setAttachment(result);
                  };
                  
                  reader.readAsDataURL(theFile);
                };

                  
        const onClearAttachment = () => {setAttachment("")};   
    
return (
<form onSubmit={onSubmit}>
    <input
        name="nweet"
        value={nweet}
        onChange={onChange}
        type="text"
        placeholder="What's on your mind?"
        maxLength={120}
    />
    <input type="file" accept="image/*" onChange={onFileChange} />
    <input type="submit" value="Nweet" />
    {attachment && (
        <div>
            <img src={attachment} width="50px" height="60px" alt=""/>
            <button onClick={onClearAttachment}>Clear</button>
        </div>
    )}
    </form>
);
};
export default NweetFactory;